.card > img {
	width: 376px;
	height: 376px;
	border-radius: 10px;
}

.card > h5 {
	color: #222230;
	font-size: 20px;
	font-weight: 500;
	padding-top: 4px;
	padding-bottom: 4px;
}
.cart > p {
	font-weight: 300;
}
@media only screen and (max-width: 500px) {
	.card > img {
		width: 276px;
		height: 276px;
		border-radius: 10px;
	}
}
